import React, { FC } from "react";

import { spacingMap } from "../styleguide";
import { uuid } from "../utils";

import {
	Label,
	RadioInput,
	RadioWrapper,
} from "./Radio.styles";
import { RadioProps } from "./Radio.types";

const Radio: FC<RadioProps> = (props) => {
	const {
		dataQA,
		id = uuid(),
		label,
		onChange = () => { },
		checked = false,
		disabled = false,
		isInvalid = false,
		mt = spacingMap.xs,
		mb = spacingMap.xs,
		mr,
		ml,
		m,
		className,
	} = props;

	return (
		<RadioWrapper data-qa={dataQA} className={className} m={m} mt={mt} mb={mb} mr={mr} ml={ml}>
			<RadioInput
				{...props}
				data-qa={`${dataQA}-input`}
				id={id}
				type="radio"
				isInvalid={isInvalid}
				onChange={(event) => !disabled && onChange(event)}
			/>
			{label
				&& (
					<Label
						data-qa={`${dataQA}-label`}
						isInvalid={isInvalid}
						isChecked={checked}
						isDisabled={disabled}
						htmlFor={id}
					>
						{label}
					</Label>
				)}
		</RadioWrapper>
	);
};

Radio.displayName = "Radio";
export default Radio;
